<template>
  <div id="__nuxt">
    <nuxt-loading ref="loading"></nuxt-loading>
    <component v-if="layout" :is="nuxt.err ? 'nuxt' : layout"></component>
  </div>
</template>

<script>
import Vue from 'vue'
import NuxtLoading from './components/nuxt-loading.vue'

import '../node_modules/bootstrap/dist/css/bootstrap.css'

import '../node_modules/highlightjs/styles/atom-one-light.css'

import '../node_modules/codemirror/lib/codemirror.css'

import '../docs/assets/css/docs.min.css'

import '../docs/assets/css/styles.css'


let layouts = {

  "_default": () => import('../docs/layouts/default.vue'  /* webpackChunkName: "layouts/default" */).then(m => m.default || m),

  "_docs": () => import('../docs/layouts/docs.vue'  /* webpackChunkName: "layouts/docs" */).then(m => m.default || m)

}

let resolvedLayouts = {}

export default {
  head: {"meta":[{"charset":"utf-8"},{"name":"viewport","content":"width=device-width, initial-scale=1, minimal-ui"},{"name":"mobile-web-app-capable","content":"yes"},{"name":"apple-mobile-web-app-title","content":"Bootstrap Vue"},{"hid":"description","name":"description","content":"Quickly integrate Bootstrap 4 components with Vue.js"},{"name":"theme-color","content":"#563d7c"},{"name":"og:type","content":"website"},{"name":"og:title","content":"Bootstrap Vue"},{"name":"og:description","content":"Quickly integrate Bootstrap 4 components with Vue.js"}],"link":[{"rel":"manifest","href":"/_nuxt/manifest.6f3167df.json"},{"rel":"shortcut icon","href":"/_nuxt/icons/icon_64.c00o00Z01E0.png"},{"rel":"apple-touch-icon","href":"/_nuxt/icons/icon_512.c00o00Z01E0.png","sizes":"512x512"}],"style":[],"script":[],"title":"Bootstrap Vue","htmlAttrs":{"lang":"en"}},
  data: () => ({
    layout: null,
    layoutName: ''
  }),
  beforeCreate () {
    Vue.util.defineReactive(this, 'nuxt', this.$options._nuxt)
  },
  created () {
    // Add this.$nuxt in child instances
    Vue.prototype.$nuxt = this
    // add to window so we can listen when ready
    if (typeof window !== 'undefined') {
      window.$nuxt = this
    }
    // Add $nuxt.error()
    this.error = this.nuxt.error
  },
  
  mounted () {
    this.$loading = this.$refs.loading
  },
  watch: {
    'nuxt.err': 'errorChanged'
  },
  
  methods: {
    
    errorChanged () {
      if (this.nuxt.err && this.$loading) {
        if (this.$loading.fail) this.$loading.fail()
        if (this.$loading.finish) this.$loading.finish()
      }
    },
    
    setLayout (layout) {
      if (!layout || !resolvedLayouts['_' + layout]) layout = 'default'
      this.layoutName = layout
      let _layout = '_' + layout
      this.layout = resolvedLayouts[_layout]
      return this.layout
    },
    loadLayout (layout) {
      if (!layout || !(layouts['_' + layout] || resolvedLayouts['_' + layout])) layout = 'default'
      let _layout = '_' + layout
      if (resolvedLayouts[_layout]) {
        return Promise.resolve(resolvedLayouts[_layout])
      }
      return layouts[_layout]()
      .then((Component) => {
        resolvedLayouts[_layout] = Component
        delete layouts[_layout]
        return resolvedLayouts[_layout]
      })
      .catch((e) => {
        if (this.$nuxt) {
          return this.$nuxt.error({ statusCode: 500, message: e.message })
        }
      })
    }
  },
  components: {
    NuxtLoading
  }
}
</script>




