import Vue from 'vue'
import VueAnalytics from 'vue-analytics'

export default async function ({ app: { router } }) {
  const moduleOptions = {"id":"UA-89526435-1","autoTracking":{"exception":true}}
  Vue.use(VueAnalytics, Object.assign({ router }, moduleOptions))
}



